import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/styles';
import Typography from '@material-ui/core/Typography';

const styles = {
  root: {
    font: `40px 'Josefin Sans', sans-serif !important`,
    color: '#FFF'
  },
};

function MyTypography(props) {
  const { classes } = props;
  return <Typography className={classes.root}>{props.children}</Typography>;
}

MyTypography.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(MyTypography);