import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import './Cvitemright.css'

const styles = theme => ({
	root: {
		flexGrow: 1,
		margin: '1em',
	},
	
	paper1: {
		width: '70%',
		height: '300px',
		margin: '0 auto',
		[theme.breakpoints.down('sm')]: {
		  width: '100%',
	    },
	},
	paper2: {
		width: '70%',
		float: 'right',
		height: '300px',
		boxShadow: 'none',
		[theme.breakpoints.down('sm')]: {
		  width: '100%',
	    },
	},

	 paper3: {
		width: '70%',
		float: 'left',
		height: '300px',
		boxShadow: 'none',
		[theme.breakpoints.down('sm')]: {
		  width: '100%',
	    },
		
	},
	period: {
		textTransform: 'none',
	    font: `700 60px 'Dancing Script', cursive !important`,
		color: 'white',
		textShadow: '3px 3px 0px rgba(0,0,0,0.6), 3px 3px 0px rgba(0,0,0,0.6) !important',
	    textAlign: 'center',
	    margin: 'auto',
	    borderBottom: '5px solid white',
	    width: '80%',
	    paddingBottom: 0,
	    top: '35%',
	  	left: '50%',
	  	transform: 'translate(-50%, -50%)',
	    cursor: 'pointer',
	},
	title: {
		textTransform: 'none',
		fontFamily: `'Josefin Sans', sans-serif !important`,
		fontSize: '30px',
		color: 'white',
		textShadow: '3px 3px 0px rgba(0,0,0,0.6), 3px 3px 0px rgba(0,0,0,0.6) !important',
	    textAlign: 'center',
	    margin: 'auto',
	    width: '80%',
	    top: '35%',
	  	left: '50%',
	  	transform: 'translate(-50%, -50%)',
	    cursor: 'pointer',
	    [theme.breakpoints.down('xs')]: {
		  fontSize: '20px',
	    },
	},

	desktop: {
		display: 'block',
	    [theme.breakpoints.down('sm')]: {
	      display: 'none',
    },
},
	mobile: {
		display: 'none',
    [theme.breakpoints.down('sm')]: {
      display: 'flex',
	}
},
exp1: {
	textAlign: 'right',
	padding: '1em',
	height: '283px',
},
exp2: {
	textAlign: 'left',
	padding: '1em',
	height: '283px',
}
});

class Cvitemright extends React.Component {
	
	state = {
			display: false,
			md: 12,
			float: null,
		};

	Show = () => {
		this.setState({display: true, md: 6, float: 'left', efect: 'animate fadeIn'});
		console.log(this.state.display);
		if (this.state.display) { this.setState({display: false, md: 12, float: null})};
	};


	render() {

	  const { classes, image, exp, period, title, color1, color2 } = this.props;

	    return (
	      <div className={classes.root}>

	             <Grid container spacing={0}>
			        
			        {this.state.display
			      	?<Grid className={classes.desktop} item xs={12} sm={12} md={6}>
						 <Paper className={classes.paper2} >
						   <div className='animate fadeInRight'>
						    <Paper className={classes.exp1} style={{background: `linear-gradient(0deg, ${color1}, ${color2})`}}>{exp}</Paper>
						   </div> 
						 </Paper>
			        </Grid> 
			      	:null
			  		}

			  		<Grid item xs={12} sm={12} md={this.state.md}>
					        <Paper className={classes.paper1} style={{background: `linear-gradient(0deg, hsla(0, 0%, 100%, 0) 0%, #000), url(${image}), center no-repeat`, backgroundSize: 'cover', float: this.state.float}}>
					          <Button className={classes.period} onClick={this.Show} size="small">{period}</Button>
					          <Button className={classes.title} onClick={this.Show} size="small">{title}</Button>
					        </Paper>
			        </Grid>

			        {this.state.display
			      	?
				      	<Grid className={classes.mobile} item xs={12} sm={12} md={6}>
							 <Paper className={classes.paper3} >
							 	<div className='animate fadeInRight'>
							    <Paper className={classes.exp2} style={{background: `linear-gradient(0deg, ${color1}, ${color2})`}}>{exp}</Paper>
							    </div>
							 </Paper>
				        </Grid> 

			      	:null
			  		}

			     </Grid>

	      </div>
	    );
  }
}

Cvitemright.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Cvitemright);